<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名、充值地址"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.introduceId">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <span>交易类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.type">
                <el-option v-for="status in typeScope" :key="status.key" :label="status.display_name"
                           :value="status.key">
                </el-option>
            </el-select>
            <span class="demonstration">创建时间</span>
            <el-date-picker
                    v-model="dateTime"
                    type="datetimerange"
                    @click-change="ceshi"
                    placeholder="选择时间范围"
                    range-separator="/"
                    @change="conditionChange">
            </el-date-picker>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
            <el-button type="primary" @click="ceshi" v-if="permission.export" size="small" style="margin-left: 1  00px">导出数据
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page"
                               :page-sizes="[15,30,45,60]" :page-size="currencyTrans.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {currencytrans, educeExcel} from '@/api/capital';
    //启用禁用筛选条件
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 2, display_name: '等待审核'},
        {key: 3, display_name: '成功提现'},
        {key: 4, display_name: '用户撤销'},
        {key: 5, display_name: '系统撤销'},
        {key: 6, display_name: '正在处理'},
        {key: 7, display_name: '正在拨币'},
        {key: 8, display_name: '拨币异常'}
    ];
    const typeScope = [
        {key: 0, display_name: '全部状态'},
        {key: 1, display_name: '充值'},
        {key: 2, display_name: '提现'}
    ];
    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    export default {
        data() {
            return {
                //搜索需求数据等
                allCoin,
                coinList: [],
                typeScope,
                dateTime: '',
                statuScope,
                tableData: [],
                total: null,
                currencyTrans: {
                    keyword: '',
                    introduceId: 0,
                    type: 0,
                    startTime: '',
                    endTime: '',
                    page: 1,
                    pageSize: 15
                },
                msg: '',
                listLoading: true,
                tableHeader: [{
                    label: "会员UID",
                    prop: "userId",
                    width: 55
                }, {
                    label: "登录名",
                    prop: "signtype",
                    width: 110,
                    align: 'center',
                    formatter(row, column, value) {
                        return value === 1 ? row.phone : (value === 2 ? row.email : '未知');
                    }
                }, {
                    label: "真实姓名",
                    prop: "realName",
                    width: 65
                }, {
                    label: "虚拟币类型",
                    prop: "currencyName",
                    width: 65,
                    align: 'center'
                }, {
                    label: "交易类型",
                    prop: "type",
                    width: 65,
                    align: 'center',
                    formatter(row, column, value) {
                        return value === 1 ? '充值' : (value === 2 ? '提现' : '未知');
                    }
                }, {
                    label: "数量",
                    prop: "amount",
                    width: 100,
                    align: 'left',
                }, {
                    label: "状态",
                    prop: "status",
                    width: 65,
                    align: 'center',
                    formatter(row, column, value) {
                        let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                        return item ? item.display_name : '';
                    }
                }, {
                    label: "确认次数",
                    // prop: "status",
                    width: 65,
                    align: 'center',
                    formatter(row, column, value) {
                        return row.confirmations + "次"
                    }
                }, {
                    label: "手续费",
                    prop: "fee",
                    align: 'center',
                    width: 45
                }, {
                    label: "交易地址",
                    prop: "transAddress",
                    width: 300,
                    align: 'left',
                    formatter(row, column, value) {
                        return row.type === 1 ? "[in] " + value : (row.type === 2 || 3 ? "[out] " + value : '未知');
                    }
                }, {
                    label: "交易id",
                    prop: "txId",
                    width: 460,
                    align: 'left',
                    formatter(row, column, value) {
                        return row.type === 1 && value !== null ? "[in] " + value : ((row.type === 2 || 3) && value !== null ? "[out] " + value : '');
                    }
                }, {
                    label: "创建时间",
                    prop: "createTime",
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }, {
                    label: "最后更新时间",
                    prop: "updateTime",
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            //测试用的区域
            ceshi() {
                let url = 'api/admin/currencytrans/nofilter/export?keyword=';
                let excelurl = '';

                if (this.dateTime[0] === undefined || this.dateTime[0] === null) {
                    this.currencyTrans.endTime = '';
                    this.currencyTrans.startTime = '';
                    excelurl = url + this.currencyTrans.keyword + '&introduceId=' + this.currencyTrans.introduceId;
                } else {
                    excelurl = url + this.currencyTrans.keyword + '&introduceId=' + this.currencyTrans.introduceId + '&startTime=' + this.currencyTrans.startTime + '&endTime=' + this.currencyTrans.endTime;
                }
                location.href = excelurl;
            },
            deriveExcel() {

                educeExcel(this.currencyTrans).then(response => {

                })
            }
            ,
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            },//获取时间段
            conditionChange(val) {
                console.log(val);
                let times = val.split('/');
                this.currencyTrans.startTime = times[0];
                this.currencyTrans.endTime = times[1];
            },
            //条件查询方法
            conditionQuery() {
                if (this.dateTime[0] === undefined || this.dateTime[0] === null) {
                    this.currencyTrans.endTime = '';
                    this.currencyTrans.startTime = '';
                    this.getList();
                } else {
                    this.getList();
                }

            },
            getList() {
                this.listLoading = true
                currencytrans(this.currencyTrans).then(response => {
                    console.log(response);
                    this.tableData = response.data.list;
                    this.coinList = response.data.coinList;
                    this.coinList.unshift(this.allCoin);
                    this.total = response.data.totalItems;
                    if (this.tableData.length === 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>



